  border-radius: 5px;
  box-shadow: 0 2px 4px;
}

input::placeholder {
  color: silver;
}

button {
  width: 80px;
  height: 20px;
  cursor: pointer;
  color: #fff;
  background-color: #000;
  border: 1px solid snow;
